var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
import uniNumberBox from '@/components/uni-number-box.vue'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			index:0,
			kaika:false,
			price:0.00,
			nums:1,
			yuanyin:'',
			wenti:'',
			array:['货品损坏','货品瑕疵','货物与订单不符'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9],
			imageList:[],
			pic:'',
			one:[],
		};
	},
	onLoad(e) {
		if(e.id){
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/order/getShouhou',
				data:{id:e.id,token:uni.getStorageSync('cbe_token')},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					uni.hideLoading();
					var one = resa.data;
					if(one.code==0){
						this.one = one.one;
						this.nums = one.one.total;
						this.price = (this.one.price * this.nums).toFixed(2);
					}
				}
			})
		}
	},
	methods:{
		changeNum(value){
			this.nums = value.number;
			this.price = (this.one.price * value.number).toFixed(2);
		},
		binput(e){
			this.wenti = e.detail.value;
		},
		del(id){
			var imageList = [];
			for(var i = 0;i<this.imageList.length;i++){
				if(i==id){
					
				}else{
					imageList.push(this.imageList[i]);
				}
			}
			this.imageList = imageList;
		},
		confirm(){
			if(this.nums==0){
				uni.showToast({ title:"请选择申请数量",icon: "none"});
				return false;
			}
			if(this.yuanyin==""){
				uni.showToast({ title:"请选择申请原因",icon: "none"});
				return false;
			}
			if(this.wenti==""){
				uni.showToast({ title:"请填写问题描述",icon: "none"});
				return false;
			}
			if(this.imageList.length == 0){
				uni.showToast({ title:"请上传您的凭证",icon: "none"});
				return false;
			}
			uni.showLoading({
				title: '正在提交'
			})
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/order/addShouhou',
				data:{id:this.one.id,orderid:this.one.orderid,nums:this.nums,yuanyin:this.yuanyin,wenti:this.wenti,pic:this.pic,token:uni.getStorageSync('cbe_token')},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					uni.hideLoading();
					var one = resa.data;
					if(one.code==0){
						uni.showToast({
							title:one.msg,icon:'success',mask:true,duration: 3000
						});
						var timer = setTimeout(()=>{
							uni.navigateBack({
								delta:1
							})
						},2000);
					}else{
						uni.showToast({ title:one.msg,icon: "none"});
					}
				}
			})
		},
		bindPickerChange: function(e) {
			this.index = e.target.value;
			var ea = e.target.value;
			this.yuanyin = this.array[ea];
			this.kaika = true;
		},
		chooseImage: async function() {
			if (this.imageList.length === 9) {
				let isContinue = await this.isFullImg();
				if (!isContinue) {
					return;
				}
			}
			var that = this;
			uni.chooseImage({
				sourceType: ['camera', 'album'],
				sizeType: ['compressed', 'original'],
				count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
				success: (res) => {
					var rl = res.tempFilePaths.length;
					for(var i=0;i<rl;i++){
						var imgsrc = res.tempFilePaths[i];
						const uploadTask = uni.uploadFile({
							url: uni.getStorageSync('hostname') + '?action=index/common/upload',
							filePath:imgsrc,
							name:"data",
							header: {'content-type' : 'application/x-www-form-urlencoded'},
							success: res => {
								var pic = that.pic;
								if(pic){
									that.pic = pic+","+res.data;
								}else{
									that.pic = res.data;
								}
								uni.showToast({
									title: '上传成功',
									icon: 'success',
									duration: 1000
								});
							},
							fail: err => {
								uni.showModal({
									content: err.errMsg,
									showCancel: false
								});
							}
						});
					}
					this.imageList = this.imageList.concat(res.tempFilePaths);
				},
				fail:(res)=> {
					
				}
			})
		},
		isFullImg: function() {
			return new Promise((res) => {
				uni.showModal({
					content: "已经有9张图片了,是否清空现有图片？",
					success: (e) => {
						if (e.confirm) {
							this.imageList = [];
							res(true);
						} else {
							res(false)
						}
					},
					fail: () => {
						res(false)
					}
				})
			})
		},
		previewImage: function(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
	}
}
